html {
  font-size: 10px;  
}
* {
  outline: none;
  outline: 0;
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: Arial, Helvetica, sans-serif;
}
.App {  
  position: relative;
  height: 100vh;
  width: 100vw;
  background: linear-gradient(135deg, #e0f3fa 0%,#d8f0fc 36%,#b8e2f6 64%,#b6dffd 100%);
  #robot_wraper {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%); 
        .quote_wraper {          
          font-size: 1.5rem;          
          position: absolute;
          left: 68%;
          top: -30%;
          .oval-thought {        
            position: relative;                                    
            padding: 3rem 2rem;
            margin: 1rem auto 8rem;
            text-align: center;
            color: #fff;
            background: #075698;
            background: -webkit-gradient(linear, left top, left bottom, from(#2e88c4), to(#075698));
            background: linear-gradient(#2e88c4, #075698);
            -webkit-border-top-left-radius: 220rem 12rem;
            -webkit-border-top-right-radius: 22rem 12rem;
            -webkit-border-bottom-right-radius: 22rem 12rem;
            -webkit-border-bottom-left-radius: 22rem 12rem;
            border-radius: 50% / 50%;
            p {
              white-space: nowrap;
            }
            &:before {
              content: "";
              position: absolute;
              bottom: -2.7rem;
              left: 2rem;
              width: 3rem;
              height: 3rem;
              background: #075698;
              border-radius: 3rem;
          }
          &:after {
            content: "";
            position: absolute;
            bottom: -3.5rem;
            left: 0;
            width: 1.5rem;
            height: 1.5rem;
            background: #075698;
            border-radius: 1.5rem;
        }
        }
        }      
    .robot {     
      width: 30rem;      
  }
  }
  #btn_wrapper {
    width: 100vw;
    height: 100vh;
    display: -webkit-box;
    display: flex;
    -webkit-box-pack: center;
            justify-content: center;
    -webkit-box-align: center;
            align-items: center;
    justify-content: space-around;
    .left_block, .right_block {
      width: 13%;
      display: -webkit-box;
      display: flex;
      -webkit-box-orient: vertical;
      -webkit-box-direction: normal;
      flex-direction: column;
      height: 100vh;
      justify-content: space-around;
      .btn {        
        transition: all 0.05s;
        box-shadow: -1px 1px 1px 1px rgba(0,0,0,0.1);
        border: 2px solid #aeaeae;
        border-radius: 50%;
        width: 100%;
        background-color: #F3F3F3; 
        cursor: pointer;
        display: -webkit-box;
        display: flex;
        -webkit-box-pack: center;
                justify-content: center;
        -webkit-box-align: center;
                align-items: center;
        img {                
        width: 67%;
        margin-left: -1px;                
      }
      &:active {              
        background-color: #fff;
        -webkit-transform: scale(0.95);
                transform: scale(0.95);
      }
      }
      .central_left {
        margin-left: -50%; 
      }
      .central_right {
        margin-left: 50%;
      }
    }
  }
}
@media screen and  (min-width: 1600px) {
  html {
    font-size: 13px;
  } 
}

@media screen and  (max-width: 1024px) {
  html {
    font-size: 9px;
  }
  .App {
   #btn_wrapper {
    .left_block, .right_block {
      width: 20%;      
    }
  } 
  }
}

@media screen and  (max-width: 768px) {
  html {
    font-size: 9px;
  }
  .App {
    #btn_wrapper {
    .left_block, .right_block {
      width: 22%;      
    }
  }
  } 
}
@media screen and  (max-width: 1020px) and (max-height: 768px) {
  html {
    font-size: 6px;
  }
  .App {
    #btn_wrapper {
    .left_block, .right_block {
      width: 14%;      
    }
  }
  }
}

@media screen and  (max-width: 576px) and (min-height: 576px),
 screen and  (max-width: 350px) and (max-height: 576px){
  html {
    font-size: 5px;
  }
  .App {
    #btn_wrapper {
       flex-direction: column;
       justify-content: space-around;
    .left_block, .right_block {
      flex-direction: row;
      width: 100vw;
      height: 18vh;
      .btn {
        width: 30%;        
        height: 0;
        padding: 15% 0;        
      }
      .central_left {
        margin-left: 0; 
        margin-top: -10%;
      }
      .central_right {
        margin-left: 0;
        margin-top: 10%;
      }
           
    }
    .left_block {
      margin-bottom: 30%;
    }
  }
  }
  
  
}
@media screen and  (max-width: 576px) and (max-height: 350px) {
  html {
    font-size: 5px;
  }
  .App {
    #btn_wrapper {
    .left_block, .right_block {
      width: 15%;      
    }
  }
  }
  
  
}